@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */

* {
  font-family: 'Press Start 2P', cursive;
}

div.container {
  text-align: center;
  padding: 50px;
  margin: 5vh auto;
  border: 2px solid black;
  background-color: white;
  color: black;
  width: 70%;
  border-radius: 10px;
  box-sizing: border-box;


}

body {
  background: crimson;
}

h1 {
  font-weight: normal;
  font-size: 40px;
}

input {
  outline: none;
  padding: 10px 10px;
  border-radius: 5px;
  background-color: white;
  border: 1px solid crimson;
  width: 65%;
  font-size: 19px;
  margin: 15px auto 0px auto;
}

input:hover {
  box-shadow: 0px 0px 1px 1px rgba(220, 20, 60, 0.8);
}

p {
  font-weight: bold;
  font-size: 50px;
}

img {
  width: 60%;

}

li {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.2s;
}

li:hover {

  background-color: crimson;
  color: white;
}

li p {
  font-size: 20px;
}

span img {
  width: 50px;
}


.pokedex img {
  width: 80px;
  height: 80px;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;

}


.container-loader {
  width: 100vw;
  height: 100vh;
  top: 0%;
  position: absolute;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
}

.lista_busquedas {
  margin-top: 0;
  height: 300px;
  overflow-y: scroll;
  width: 65%;
  margin: auto;
}

.loader {
  border: 16px solid #f3f3f3;
  /* Light grey */
  border-top: 16px solid crimson;
  /* Blue */
  border-radius: 50%;
  width: 90px;
  height: 90px;
  background: url(/static/media/pokeball.1faeca5f.png);
  background-size: cover;
  animation: spin 1s linear infinite;
  padding: 10px;
  margin: 0px auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
    border: 16px solid white;
    background-color: crimson;
  }

  25% {
    border: 8px solid crimson;
    background-color: white;
  }

  50% {
    border: 0px solid crimson;
    background-color: white;

  }

  75% {
    border: 8px solid white;
    background-color: crimson;
  }

  100% {
    transform: rotate(360deg);
    border: 16px solid white;
    background-color: crimson;
  }
}

@media (max-width:1000px) {
  div.container {
    width: 100%;
    border: none;
    margin: 0;
    border-radius: 0;
    background-color: rgb(255, 255, 255);
  }


  .container-loader p {
    font-size: 25px;
    text-align: center;
  }

  .lista_busquedas {
    height: 250px;
    overflow-y: scroll;
    scrollbar-width: 2px;
    width: 95%;
  }

  li p {

    font-size: 14px;
  }

  p {
    font-size: 14px;
  }

  input {
    width: 95%;
    font-size: 13.5px;
    padding: 10px 2px;
  }


  p {
    font-size: 20px;
  }
}
